/* Header */
#header {
  color: #fff;
}

/* Navbar */
.header-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  color: #fff;
  background-color: #ff9b6a;
  padding: 0 50px;
  position: fixed;
  top: 0px;
  z-index: 11;
}
.header-navbar img {
  height: 100%;
  max-width:300px;
}

.header-navbar a {
  color: #fff;
  padding: 10px 20px;
  margin: 0 5px;
  font-size: 1.4rem;
}

.header-navbar a:hover {
  border-bottom: #333 2px solid;
}

.header-navbar ul {
  display: flex;
}

/* Banner */

.header-banner {
  background: url('../../images/banner/banner4.jpg') no-repeat center center/cover;
  height: 80vh;
  position: relative;
}

.header-banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);

}

.header-banner * {
  z-index: 10;
}

.header-banner .header-banner-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100%;
  padding:  0 20px;
}

.header-banner .header-banner-content h1 {
  font-size: 55px;
  font-weight: 300;
}

.header-banner .header-banner-content p {
  font-size: 45px;
  font-weight: 300;
  max-width: 600px;
  margin: 20px 0 30px;
}


