@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400&display=swap);
/* Header */
#header {
  color: #fff;
}

/* Navbar */
.header-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  color: #fff;
  background-color: #ff9b6a;
  padding: 0 50px;
  position: fixed;
  top: 0px;
  z-index: 11;
}
.header-navbar img {
  height: 100%;
  max-width:300px;
}

.header-navbar a {
  color: #fff;
  padding: 10px 20px;
  margin: 0 5px;
  font-size: 1.4rem;
}

.header-navbar a:hover {
  border-bottom: #333 2px solid;
}

.header-navbar ul {
  display: flex;
}

/* Banner */

.header-banner {
  background: url(/react-happyhwa/static/media/banner4.e77200a4.jpg) no-repeat center center/cover;
  height: 80vh;
  position: relative;
}

.header-banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);

}

.header-banner * {
  z-index: 10;
}

.header-banner .header-banner-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100%;
  padding:  0 20px;
}

.header-banner .header-banner-content h1 {
  font-size: 55px;
  font-weight: 300;
}

.header-banner .header-banner-content p {
  font-size: 45px;
  font-weight: 300;
  max-width: 600px;
  margin: 20px 0 30px;
}



.icons {
  padding: 30px;
}

.icons h3 {
  font-weight: bold;
  font-size: 25px;
  margin-bottom: 15px;
}

.icons img {
  max-width: 300px;
  border-radius: 10%;
}
#news {
  background-color: #bb6968;;
}



#news .column-2 ul {
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 justify-content: center;
 width: 100%;
 text-align: start;
 padding: 25px;
 font-size: 23px;
 color: #fff;
}
#news .column-2 h2 {
  font-size: 40px;
  font-weight: 400;
  margin-top: 40px;
  margin-bottom: 40px;
  color: #fff;
}

#news .column-2 li {
  border-bottom: 2px solid #ff9b6a;
  width: 100%;
  margin-bottom: 15px;
  padding-bottom: 5px;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Noto Sans TC', sans-serif;
  line-height: 1.6;
  color: #333;
  background: #fff;
}

ul {
  list-style: none;
}

a {
  color: #333;
  text-decoration: none;
}

h1 ,h2 {
  font-weight: 300;
  line-height: 1.2;
}

p {
  margin: 10px 0;
}

img {
  width: 100%;
}

/* Utilities */

/* Button */
.bg-light {
  background: #f4f4f4;
  color: #333;
}

/* Flex Items */

.flex-items {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.flex-items > div {
  padding: 20px;
}

/* Flex columns */
.flex-columns .row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.flex-columns .column {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.flex-columns .column-2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  padding: 30px;
}

.flex-columns .column .column-1,
.flex-columns .column .column-2 {
  height: 100%;
}

.flex-columns img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
